/* Responsive */
@define-mixin mobile {
  @media screen and (width <= $tablet - 1px) {
    @mixin-content;
  }
}

@define-mixin tablet {
  @media screen and (width >= $tablet) {
    @mixin-content;
  }
}

@define-mixin touch {
  @media screen and (width <= $desktop - 1px) {
    @mixin-content;
  }
}

@define-mixin desktop {
  @media screen and (width >= $desktop) {
    @mixin-content;
  }
}

@define-mixin widescreen {
  @media screen and (width >= $widescreen) {
    @mixin-content;
  }
}

/* Draw dropdown arrow */
@define-mixin arrow $color {
  border: 1px solid $color;
  border-right: 0;
  border-top: 0;
  content: ' ';
  display: block;
  height: 7px;
  pointer-events: none;
  position: absolute;
  transform: rotate(-45deg);
  width: 7px;
}

@define-mixin unselectable {
  /* This is to prevent control be selected in iOS Safari */
  -webkit-touch-callout: none;
  /* General disable of selection for controls */
  user-select: none;
}
