$reference-toc-width: 226px;
$reference-padding: 20px;

/* Navbar */
.reference-nav {
	padding: 12px 20px;
	border-bottom: 1px solid $border;
}

/* Article */
.reference-content {
  @mixin content;

  /* ~16px */
  font-size: 1.142857em;
  padding-left: $reference-padding;
  padding-right: $reference-padding;

  & h6 {
    /*margin-top: 20px;*/
  }

  & .alert {
    background: $white;
    border: 1px solid color($border alpha(50%));
    padding: 12px;

    & li {
      margin-top: 0;
    }

    & h5 {
      color: $text-strong;
      font-size: 1rem;
      font-weight: 400;
      letter-spacing: 0.65px;
      margin: 0.35em 0;
      text-transform: uppercase;
    }

    & p {
      margin: 0;
    }
  }
}

/* Use position model to layout the toc sidebar and body area */
.reference-toc {
  background: $background;
  position: absolute 0 auto 0 0;
  width: $reference-toc-width;
}

.reference-body {
  position: absolute 0 0 0 $reference-toc-width;
  width: auto;
}

.reference {
  box-sizing: border-box;
  position: absolute 96px 0 0 0;
  overflow: auto;

  & .reference-toc,
  & .reference-body {
    overflow-y: auto;
  }
}

/* ------ */
/* TODO: Will deprecate when we start to touch the rendered content area */
.header,
.level {
  @mixin grids;

  & > .primary,
  & > .secondary {
    flex: 1;
    padding: 10px;
    max-width: 50%;
    width: 50%;
  }

  & > .secondary {
    padding-left: 24px;
  }
}

.header {
  padding-left: 10px;
  padding-right: @padding-left;
}

.list-clean {
  list-style-type: none;
  padding: 0;
}
