.navbar-item {
	& .title,
	& .subtitle {
		margin-bottom: 0;
	}

	& {
		@mixin mobile {
			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}
	}

	/* Reset the p margin */
	& > p {
		margin: 0;
	}
}

.navbar {
	@mixin block;

	& {
		@mixin tablet {
			align-items: center;
			display: flex;
			/*justify-content: space-between;*/

			& > .navbar-item {
				flex: 1;
			}
		}
	}

	/* To enable using ul>li within navbar */
	& > ul {
		list-style-type: none;
		margin: 0;
		padding: 0;
		& > li {
			display: inline-block;
			list-style-type: none;
		}
	}
}

.navbar-left,
.navbar-right {
	& .navbar-item {
		&.is-flexible {
			flex: 1;
		}
		&:not(:last-child) {
			margin-right: 10px;
		}
	}
}

.navbar-left {
	& {
		@mixin mobile {
			& + .navbar-right {
				margin-top: 20px;
			}
		}

		@mixin tablet {
			align-items: center;
			display: flex;

			&:not(:last-child) {
				margin-right: 20px;
			}
		}
	}
}

.navbar-right {
	& {
		@mixin tablet {
			align-items: center;
			display: flex;
			justify-content: flex-end;
		}
	}
}